export default {
  name: "Photo",
  transition: "page",
  layout: "details",
  head () {
    return {
      title: 'Bill Funk Photo Gallery',
    }
  },
  async asyncData({ $prismic, params, error }) {
    try {
      const post = (await $prismic.api.getByUID('photo', params.uid)).data


      return {
        document: post,
      };
    } catch (e) {
      error({ statusCode: 404, message: "Page not found" });
    }
  }
};

